.sidebar {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    padding-top: 230px;
    height: 100%;
    width: 260px;
    background-color: #2F323A;

    transition: .4s;
}

.sidebar > a {
    text-decoration: none;
    width: 100%;
    line-height: 60px;
    padding-left: 40px;
    display: flex;
    align-items: center;
    color: white;

    transition: .4s;
    transition-property: background-color;
}

.sidebar > a > img {
    width: 20px;
    height: 20px;
}

.sidebar > a > span {
    display: inline-block;
    margin-left: 12px;
    margin-bottom: 4px;

}

.sidebarClosed {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -190px;
    height: 100%;
    width: 260px;
    background-color: #2F323A;
    padding-top: 230px;
    transition: .4s;
}

.sidebarClosed > a > span {
    display: none;
}

.sidebarClosed > a {
    align-items: center;
    margin-left: 170px;
    padding-left: 40px;
    color: white;
    line-height: 60px;

    transition: .4s;
    transition-property: background-color;
}


.sidebarClosed > a > img {
    width: 20px;
    height: 20px;
    display: inline-block;
    line-height: 60px;
    margin-top: 20px;
}


.editableSidebar > img:active {
    box-shadow: 0 5px 20px 9px rgba(34, 60, 80, 0.15);
}


.navItem:hover {
    background-color: #0772e3;
}

.navItem.active {
    background-color: #0772e3;
}

